<template>
  <div :class="$root.themeHomeChange === '1' ? 'defectView_light' : 'defectView'">
    <!-- <div class="header">风险监测情况</div> -->
    <el-form
      :inline="true"
      class="selectBox"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="缺陷等级: ">
          <el-select
            size="mini"
            v-model="params.defectLevel"
            placeholder="请选择"
            style="width: 150px;"
            clearable
            @change="queryTableList(true)"
          >
            <el-option label="轻微缺陷" value="轻微缺陷"></el-option>
            <el-option label="一般缺陷" value="一般缺陷"></el-option>
            <el-option label="较大缺陷" value="较大缺陷"></el-option>
            <el-option label="重大缺陷" value="重大缺陷"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="责任部门: ">
          <el-select
            size="mini"
            v-model="params.department"
            placeholder="请选择"
            style="width: 150px;"
            clearable
            @change="queryTableList(true)"
          >
            <el-option
              :label="item.departmentName"
              :value="item.departmentCode"
              v-for="(item, index) in departmentList"
              :key="index"
            ></el-option>
            <!-- <el-option label="办公室" value="办公室"></el-option>
            <el-option label="合规与监审部" value="合规与监审部"></el-option>
            <el-option label="系统规划部" value="系统规划部"></el-option>
            <el-option label="资产管理部" value="资产管理部"></el-option>
            <el-option label="市场营销部" value="市场营销部"></el-option>
            <el-option label="安全监管部" value="安全监管部"></el-option>
            <el-option label="人力资源部" value="人力资源部"></el-option>
            <el-option label="党建工作部" value="党建工作部"></el-option>
            <el-option label="创新与数字化部" value="创新与数字化部"></el-option>
            <el-option label="财务部" value="财务部"></el-option> -->
          </el-select>
        </el-form-item>
        <el-form-item label="单据时间">
          <el-date-picker
            v-model="params.djsj"
            size="mini"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            style="width: 220px;"
            @change="queryTableList(true)"
          >
          </el-date-picker>
        </el-form-item>
      </span>
      <el-form-item>
        <bda-search-input
          v-model="params.keyword"
          placeholder="请输入关键字"
          @search="queryTableList(true)"
          @clear="queryTableList(true)"
          size="mini"
        >
        </bda-search-input>
      </el-form-item>
    </el-form>
    <div class="content">
      <el-table
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        v-loading="tableLoading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="orderId"
          label="单据编号"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="name"
          label="任务 / 单据名称"
          min-width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="defectLevel"
          label="缺陷等级"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="orderStatus"
          label="事件状态"
          width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="department"
          label="责任部门"
          width="130"
          align="center">
        </el-table-column>
        <el-table-column
          prop="orderDate"
          label="单据日期"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="operation"
          label="操作"
          width="150"
          align="center">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row, 'detail')" type="text">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import Interface from "@/base/mixin/disposal-center.js";
  export default {
    name: "defectView",
    mixins: [Interface],
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        tableLoading: false,
        tableHeight: window.innerHeight - 175,
        departmentList: [],
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0
        },
        tableData: [
          // {
          //   id: 'CJ-202110100001',
          //   name: '2021年岗位不相容检查任务',
          //   defectLevel: '较大缺陷',
          //   department: '安全监管部',
          //   status: '整改中',
          //   date: '2021-10-10'
          // }
        ]
      }
    },
    created() {
      this.queryTableList(true);
      this.getDepartmentList();
    },
    // 监听
    watch: {},
    mounted() {},
    methods: {
      // 查询责任部门
      getDepartmentList() {
        this.getDisposalSelect().then((rs)=>{
          // window.console.log(rs)
          if(rs) {
            this.departmentList = rs;
          } else {
            this.departmentList = [];
          }
        }).finally(()=>{})
      },
      // 查询表格内容
      queryTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          begin: Array.isArray(this.params.djsj) && this.params.djsj[0] ? this.params.djsj[0] : '' ,
          end: Array.isArray(this.params.djsj) && this.params.djsj[1] ? this.params.djsj[1] : '',
          current: this.params.currentPage,
          size: this.params.pageSize,
          keyword: this.params.keyword,
          department:  this.params.department,
          defectLevel: this.params.defectLevel,
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getOrderList(params).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 表格操作按钮点击
      handleClick(row, type) {
        window.console.log(`类型：${type} , 行内容:`, row);
        this.$router.push({
          path: '/details',
          query: {
            index: '4-2',
            params: window.btoa(window.encodeURIComponent(JSON.stringify({
              name: row.name,
              orderId: row.orderId || this.$route.query.orderId,
            }))),
            actNav: this.$route.query.actNav
          }
        });
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.pageSize = val;
        this.queryTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.queryTableList();
      }
    }
  }
</script>

